import React from 'react';
import {Link} from 'react-router-dom';
import './events-preview.styles.css';

import EventPreviewCard from '../event-preview-card/event-preview-card.component'
const EventsPreview = () => (
  <div  className='events-preview'>
    <div className='events-preview-header'>
      <h1>EVENTS</h1> 
      <Link to='/events'>See all</Link>     
    </div>
    <div className='events-preview-list'>
      <EventPreviewCard />
      <EventPreviewCard />
      <EventPreviewCard />     
      <EventPreviewCard /> 
      <EventPreviewCard />   
      <EventPreviewCard />                                
    </div>
    
  </div>
    
)

export default EventsPreview;