import React from 'react';
import './event-preview-card.styles.css';

const EventPreviewCard = () => (
    <div className='event-preview-card'>
      <div className="event-picture">
        <div className="heart-icon">
           <ion-icon size='large' name="heart-outline"></ion-icon>
        </div>        
        <div className="event-popularity">12 DEC</div>
      </div>
      <div className="event-details">
        <h2 className="event-name">Do's and Don't of online business</h2>
        <p className="event-host"><i>by Nitin Pammani</i></p>
      </div> 
    </div>
)

export default EventPreviewCard

  